.chatComponent .outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}


.chatComponent .container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: #FFF;
  border-radius: 8px;
  height: 65%;
  width: 50%;
  border: 1px solid #cabfbf;
}

@media (min-width: 320px) and (max-width: 480px) {
  .chatComponent .outerContainer {
    height: 100%;
  }

  .chatComponent .container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .chatComponent .container {
    width: 60%;
  }
}