.inputComponent .form {
  display: flex;
  border-top: 2px solid #D3D3D3;
}

.inputComponent .input {
  border: none;
  border-radius: 0;
  padding: 3%;
  width: 80%;
  font-size: 1em;
}

.inputComponent input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.inputComponent .sendButton {
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979FF;
  padding: 10px;
  display: inline-block;
  border: none;
  width: 20%;
}